:root {
  --accent-dark-color: #333;
  --accent-dark-color-hover: #131313;
  --accent-light-color: #fff;
  --light-header-color: #bdbdbd;
  --text-dark-color: #000;
  --background-btn: #f9f9f9;
  --background-color: #fbfbfb;
  --gray-6: #f2f2f2;
  --input-background: #f3f3f3;
  --footer-bg: #2c2c2c;
}

@font-face {
  font-family: Hoves;
  font-display: swap;
  src: url("TTHoves-Light.03ee9a61.ttf") format("truetype"), url("TTHoves-Light.e155b8e3.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: Hoves;
  font-display: swap;
  src: url("TTHoves-Regular.1613f208.ttf") format("truetype"), url("TTHoves-Regular.772ba4be.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Hoves;
  font-display: swap;
  src: url("TTHoves-Bold.fb62742a.ttf") format("truetype"), url("TTHoves-Bold.5d5a23cb.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

html {
  font-family: Hoves, sans-serif;
}

header {
  position: relative;
}

.header {
  align-items: center;
  display: flex;
}

.header__logo {
  flex: auto;
  padding: 15px 0;
}

.header__logo a {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.header__navigation {
  flex: auto;
}

.header__navigation ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__navigation ul li {
  flex: auto;
}

.header__navigation ul li a {
  color: var(--accent-dark-color);
  text-transform: uppercase;
  letter-spacing: 2.4px;
  padding: 30px 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  text-decoration: none;
  position: relative;
}

.header__navigation ul li a:before, .header__navigation ul li a:after {
  content: "";
  background-color: var(--accent-dark-color);
  width: 0;
  height: 1px;
  transition: width .2s;
  position: absolute;
  left: 0;
}

.header__navigation ul li a:before {
  top: 33%;
}

.header__navigation ul li a:after {
  bottom: 34%;
}

.header__navigation ul li a:hover:before, .header__navigation ul li a:hover:after {
  width: 100%;
  transition: width .2s;
}

.header__navigation-mobile-btn {
  background-color: #0000;
  width: 30px;
  height: 33px;
  transition: background-color .2s;
  display: none;
  position: relative;
}

.header__navigation-mobile-btn:hover {
  cursor: pointer;
}

.header__navigation-mobile-btn .mobile-line {
  background-color: #000;
  width: 100%;
  height: 3px;
  position: absolute;
}

.header__navigation-mobile-btn .mobile-line:first-child {
  transition: transform .3s;
  top: 15%;
  left: 0;
  transform: translateY(0)rotate(0);
}

.header__navigation-mobile-btn .mobile-line:nth-child(2) {
  opacity: 1;
  transition: opacity .1s;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.header__navigation-mobile-btn .mobile-line:nth-child(3) {
  transition: transform .3s;
  bottom: 15%;
  left: 0;
  transform: translateY(0)rotate(0);
}

.header__navigation-mobile-btn-active .mobile-line:first-child {
  transition: transform .3s;
  transform: translateY(10px)rotate(-45deg);
}

.header__navigation-mobile-btn-active .mobile-line:nth-child(2) {
  opacity: 0;
  transition: opacity .1s;
}

.header__navigation-mobile-btn-active .mobile-line:nth-child(3) {
  transition: transform .3s;
  transform: translateY(-10px)rotate(45deg);
}

.header__navigation-mobile {
  background-color: var(--accent-light-color);
  z-index: 5;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  transition: left .4s;
  display: flex;
  position: absolute;
  top: 0;
  left: -100%;
}

.header__navigation-mobile ul {
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__navigation-mobile ul li {
  justify-content: center;
  width: 100%;
  display: flex;
}

.header__navigation-mobile ul li a {
  color: var(--accent-dark-color);
  text-transform: uppercase;
  letter-spacing: 2.4px;
  padding: 15px 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  text-decoration: none;
}

.header__navigation-mobile ul li .mobile-close {
  background-color: #0000;
  width: 30px;
  height: 31px;
  position: relative;
}

.header__navigation-mobile ul li .mobile-close .close-line {
  background-color: #000;
  width: 100%;
  height: 2px;
  position: absolute;
}

.header__navigation-mobile ul li .mobile-close .close-line:first-child {
  top: 15%;
  left: 0;
  transform: translateY(10px)rotate(-45deg);
}

.header__navigation-mobile ul li .mobile-close .close-line:nth-child(2) {
  transition: transform .3s;
  bottom: 15%;
  left: 0;
  transform: translateY(-10px)rotate(45deg);
}

.header__navigation-mobile ul li:last-child {
  padding-top: 50px;
}

.header__navigation-mobile ul li:last-child:hover {
  cursor: pointer;
}

.header__navigation-mobile-active {
  transition: left .4s;
  left: 0;
}

@media (max-width: 768px) {
  .header__navigation {
    display: none;
  }

  .header__navigation-mobile-btn {
    display: block;
  }
}

.footer {
  background-color: var(--footer-bg);
  position: relative;
}

.footer:after {
  content: "";
  background-color: var(--accent-light-color);
  opacity: .1;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 70px;
  left: 0;
}

.footer__wrapper {
  justify-content: space-between;
  padding-top: 58px;
  display: flex;
}

.footer__logo, .footer__info {
  margin-right: 30px;
}

.footer__info ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer__info ul li {
  margin-bottom: 16px;
}

.footer__info ul li:last-child {
  margin-bottom: 0;
}

.footer__info ul li a {
  color: var(--accent-light-color);
  letter-spacing: .42px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
}

.footer__subheader h3 {
  color: var(--accent-light-color);
  margin: 0 0 25px;
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
}

.footer__contacts {
  flex-direction: column;
  justify-content: flex-start;
  max-width: 310px;
  margin-right: 30px;
  display: flex;
}

.footer__geo, .footer__phone, .footer__email {
  align-items: flex-start;
  margin-bottom: 30px;
  display: flex;
}

.footer__email {
  margin-bottom: 0;
}

.footer__icon {
  width: 15px;
  height: 15px;
  margin-right: 15px;
}

.footer__description, .footer__description a {
  color: var(--accent-light-color);
  letter-spacing: .42px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
}

.footer__mediaset {
  display: flex;
}

.footer__icon-wrapper {
  width: 15px;
  height: 15px;
  margin-right: 40px;
}

.footer__icon-wrapper:last-child {
  margin-right: 0;
}

.footer__icon-wrapper svg {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.footer__rights {
  color: #c8c8c880;
  justify-content: center;
  padding: 84px 0 24px;
  font-family: Open Sans, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  display: flex;
}

@media (max-width: 991px) {
  .footer__social {
    display: none;
  }
}

@media (max-width: 768px) {
  .footer__wrapper {
    flex-wrap: wrap;
  }

  .footer__logo {
    justify-content: center;
    width: 100%;
    margin-bottom: 20px;
    margin-right: 0;
    display: flex;
  }

  .footer__info, .footer__contacts {
    width: 33.3333%;
    margin-right: 0;
  }

  .footer__social {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin: 30px 0 0;
    display: flex;
  }
}

@media (max-width: 575px) {
  .footer__logo {
    margin-bottom: 0;
  }

  .footer__info, .footer__contacts {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 100%;
    margin-top: 40px;
    display: flex;
  }

  .footer__nav {
    width: 100%;
  }

  .footer__info ul {
    flex-wrap: wrap;
    justify-content: space-between;
    display: flex;
  }

  .footer__info li {
    margin-right: 20px;
  }

  .footer__geo, .footer__phone, .footer__email {
    margin-bottom: 10px;
  }

  .footer__geo {
    max-width: 70%;
  }
}

.btn-slider {
  background-color: var(--accent-light-color);
  z-index: 2;
  border: 1px solid #f2f2f2;
  width: 53px;
  height: 53px;
  margin: 0;
  padding: 0;
  transition: background-color .2s;
  position: relative;
}

.btn-slider-left svg {
  rotate: 180deg;
}

.btn-slider-right {
  margin-left: 22px;
}

.btn-slider:hover {
  cursor: pointer;
  background-color: var(--background-btn);
  transition: background-color .2s;
}

.btn-slider-details, .btn-about, .btn-allprojects, .btn-submit {
  background-color: var(--accent-light-color);
  border: none;
  padding: 30px 50px;
  transition: background-color .2s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.btn-slider-details a, .btn-about a, .btn-allprojects a, .btn-submit a {
  color: var(--accent-dark-color);
  letter-spacing: 2.4px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  text-decoration: none;
}

.btn-slider-details:hover, .btn-about:hover, .btn-allprojects:hover, .btn-submit:hover {
  background-color: var(--background-btn);
  transition: background-color .2s;
}

.btn-about {
  margin-top: 30px;
  padding: 26px 70px;
  position: static;
}

.btn-projects {
  color: var(--accent-light-color);
  letter-spacing: 2.4px;
  text-transform: uppercase;
  background: none;
  border: none;
  margin-top: 14px;
  font-family: Gothic A1, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
}

.btn-projects svg path {
  fill: var(--accent-light-color);
}

.btn-projects:hover {
  cursor: pointer;
}

.btn-allprojects {
  background-color: var(--accent-dark-color);
  position: static;
}

.btn-allprojects a {
  color: var(--accent-light-color);
}

.btn-allprojects svg path {
  fill: var(--accent-light-color);
}

.btn-allprojects:hover {
  background-color: var(--accent-dark-color-hover);
}

.btn-submit {
  background-color: var(--accent-dark-color);
  color: var(--accent-light-color);
  letter-spacing: 2.4px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  text-decoration: none;
  position: static;
}

.btn-submit svg path {
  fill: var(--accent-light-color);
}

.btn-submit:disabled {
  background-color: #575757;
}

.btn-submit:hover:enabled {
  background-color: var(--accent-dark-color-hover);
  cursor: pointer;
}

.promo {
  margin-bottom: 120px;
}

.promo__wrapper {
  height: 830px;
  display: flex;
}

.promo__start {
  flex-direction: column;
  justify-content: center;
  width: 35%;
  display: flex;
}

.promo__header {
  max-width: 275px;
  margin-right: 20px;
  font-size: 64px;
  line-height: 64px;
}

.promo__header span.light {
  color: var(--light-header-color);
  text-transform: uppercase;
  font-weight: 300;
}

.promo__header span.dark {
  color: var(--accent-dark-color);
  text-transform: uppercase;
  font-weight: 700;
}

.promo__arrows {
  margin-top: 90px;
  position: relative;
}

.promo__arrows:after {
  content: "";
  z-index: 1;
  background-color: #f2f2f2;
  width: 80%;
  height: 1px;
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
}

.promo__counter {
  justify-content: space-between;
  max-width: 130px;
  margin-top: 90px;
  display: flex;
  position: relative;
}

.promo__counter span {
  color: var(--light-header-color);
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.promo__counter:after {
  content: "";
  background-color: #e0e0e0;
  width: 2px;
  height: 40px;
  position: absolute;
  bottom: -10px;
  left: 20%;
  transform: translateX(35px)rotate(45deg);
}

.promo__end {
  align-items: center;
  width: 65%;
  display: flex;
}

.promo .slider {
  position: relative;
  overflow: hidden;
}

.promo .slider__wrapper {
  transition: transform .3s;
  display: flex;
}

.promo .slider__slide {
  width: 770px;
  height: 830px;
  position: relative;
}

.promo .slider__slide img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

@media (max-width: 991px) {
  .promo {
    margin-bottom: 40px;
  }

  .promo__wrapper {
    flex-direction: column;
  }

  .promo__start {
    align-items: center;
    width: 100%;
  }

  .promo__header {
    max-width: none;
    margin-top: 60px;
    margin-right: 0;
  }

  .promo__arrows {
    justify-content: space-between;
    width: 45%;
    margin-top: 50px;
    display: flex;
  }

  .promo__arrows .btn-slider-right {
    margin-left: 0;
  }

  .promo__arrows:after {
    content: none;
  }

  .promo__counter {
    width: 20%;
    max-width: none;
    margin-top: 50px;
  }

  .promo__end {
    width: 100%;
    margin-top: 50px;
  }

  .promo .slider__slide {
    width: 870px;
    height: 480px;
  }
}

@media (max-width: 575px) {
  .promo__header {
    margin-top: 40px;
    font-size: 50px;
    line-height: 50px;
  }

  .promo__arrows {
    margin-top: 40px;
  }

  .promo__counter {
    width: 36%;
    margin-top: 40px;
  }
}

.about {
  margin-bottom: 150px;
}

.about__wrapper {
  background-color: var(--background-color);
  box-sizing: border-box;
  justify-content: space-between;
  height: 435px;
  padding: 30px 70px 30px 100px;
  display: flex;
}

.about__pictures {
  justify-content: flex-start;
  align-items: center;
  width: 60%;
  margin-top: 50px;
  display: flex;
}

.about__col-one {
  flex-direction: column;
  flex: auto;
  display: flex;
}

.about__col-one picture:last-child {
  margin-top: 30px;
}

.about__col-one img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.about__col-two {
  flex: auto;
  margin-left: 30px;
}

.about__col-two img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.about__text {
  width: 400px;
  margin-left: 30px;
  position: relative;
}

.about__header {
  color: var(--light-header-color);
  font-size: 64px;
  font-weight: 300;
  line-height: 64px;
}

.about__description {
  color: var(--text-dark-color);
  width: 400px;
  margin-top: 20px;
  font-size: 16px;
  font-weight: 300;
  line-height: 25px;
}

@media (max-width: 1200px) {
  .about__wrapper {
    flex-direction: column;
    height: 100%;
  }

  .about__pictures {
    width: 100%;
  }

  .about__col-one, .about__col-two {
    max-width: 270px;
  }

  .about__text {
    width: 100%;
    margin-top: 30px;
    margin-left: 0;
  }

  .about__description {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .about__wrapper {
    padding: 25px;
  }
}

@media (max-width: 575px) {
  .about {
    margin-bottom: 40px;
  }

  .about__pictures {
    justify-content: center;
    margin-top: 0;
  }

  .about__header {
    font-size: 50px;
  }

  .about__col-two {
    display: none;
  }
}

.goals {
  margin-bottom: 120px;
}

.goals__header {
  color: var(--light-header-color);
  font-size: 64px;
  font-weight: 300;
  line-height: 64px;
}

.goals__wrapper {
  justify-content: space-between;
  display: flex;
}

.goals__one, .goals__two {
  align-items: center;
  width: 50%;
  display: flex;
}

.goals__one .goals__description {
