}

.goals__two .goals__description {
  max-width: 420px;
}

.goals__number {
  color: var(--gray-6);
  margin-right: 30px;
  font-family: SF Pro Display, sans-serif;
  font-size: 200px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
}

.goals__description {
  color: var(--accent-dark-color);
  font-size: 22px;
  font-weight: 400;
  line-height: 36px;
}

@media (max-width: 991px) {
  .goals__wrapper {
    flex-direction: column;
  }

  .goals__one, .goals__two {
    width: 100%;
  }

  .goals__one .goals__description, .goals__two .goals__description {
    max-width: 100%;
  }
}

@media (max-width: 575px) {
  .goals {
    margin-bottom: 40px;
  }

  .goals__header {
    font-size: 50px;
  }

  .goals__number {
    font-size: 125px;
  }

  .goals__one, .goals__two {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 30px;
  }
}

.projects {
  margin-bottom: 120px;
}

.projects__header {
  color: var(--light-header-color);
  margin-bottom: 60px;
  font-size: 64px;
  font-weight: 300;
  line-height: 64px;
}

.projects__grid {
  grid-template-rows: 255px;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-rows: 255px;
  gap: 30px;
